    /* text-align: center; */
}

.title-view {
    width: 100px;
    flex-grow: 1;
}

.search-bar {
    color: rgb(0, 0, 0);
    font-size: 20px;
    width: 200%;
    height: 50px;
    /* margin-right: 150px; */
    /* flex-grow: 1; */
}

.search-bar-view {
    margin-right: 10%;
    width: 50%;
}

